@mixin text-size-adjust($value: none) {
    -webkit-text-size-adjust: $value;
    -moz-text-size-adjust: $value;
    -ms-text-size-adjust: $value;
}
@mixin box-sizing($value: border-box) {
    // -webkit-box-sizing: $value;
    // -moz-box-sizing: $value;
    box-sizing: $value;
}
@mixin box-shadow($position, $color, $inset:"") {
    -webkit-box-shadow:$position $color #{$inset};
    -moz-box-shadow:$position $color #{$inset};
    box-shadow:$position $color #{$inset};
}
@mixin user-select($value: none) {
    -webkit-user-select:$value;
    // -khtml-user-select:$value;
    // -moz-user-select:$value;
    // -ms-user-select:$value;
    // user-select:$value;
}
@mixin text-shadow($v:'0', $h:'0', $b:'0', $color:'#FF0000') {
  text-shadow: $v $h $b $color;
    // -webkit-user-select:$value;
    // -khtml-user-select:$value;
    // -moz-user-select:$value;
    // -ms-user-select:$value;
    // user-select:$value;
}
@mixin opacity($decimal:1){
    opacity: $decimal;
    $percent: $decimal * 100;
    filter:alpha(opacity=$percent);
    -ms-filter:"alpha(opacity=#{$percent})";
    filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=#{round($percent)});
}
@mixin scrollbar($bar:10,$border:100,$color:#bdbdbd){
    ::-webkit-scrollbar { width:#{$bar}px;}
    ::-webkit-scrollbar-track {
      // -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
      // border-radius: 0;
      // background-color:lighten($ColorMain,10);
      background-color: $ColorBackground;
    }
    ::-webkit-scrollbar-thumb { background-color:#{$color};border-top-left-radius:#{$border}px;border-top-right-radius:#{$border}px;border-bottom-right-radius:#{$border}px;border-bottom-left-radius:#{$border}px;}
    ::-webkit-scrollbar-thumb:window-inactive {background-color:#{$color};}
}
